'use client'

import { useState, useRef } from 'react'
import TextInput from './text-input'
import FileUpload from './file-upload'

interface DocumentInputProps {
  category: string
}

export default function DocumentInput({ category }: DocumentInputProps) {
  const [textContent, setTextContent] = useState('')
  const [uploadedFile, setUploadedFile] = useState<File | null>(null)

  return (
    <div className="space-y-8">
      {/* Text Input Section */}
      <div className="space-y-4">
        <label className="text-sm font-semibold text-muted-foreground uppercase tracking-wide">
          Paste your text here
        </label>
        <TextInput
          value={textContent}
          onChange={setTextContent}
          placeholder="Paste your legal document text here..."
        />
      </div>

      {/* Divider */}
      <div className="flex items-center gap-4">
        <div className="flex-1 h-px bg-gradient-to-r from-border via-accent/30 to-border"></div>
        <span className="text-xs uppercase text-muted-foreground font-medium">Or</span>
        <div className="flex-1 h-px bg-gradient-to-r from-border via-accent/30 to-border"></div>
      </div>

      {/* File Upload Section */}
      <div className="space-y-4">
        <label className="text-sm font-semibold text-muted-foreground uppercase tracking-wide">
          Or upload a document
        </label>
        <FileUpload
          uploadedFile={uploadedFile}
          onFileSelect={setUploadedFile}
          acceptedFormats={['.txt', '.pdf', '.docx']}
        />
      </div>

      {/* CTA Button */}
      <button
        disabled={!textContent.trim() && !uploadedFile}
        className="premium-button w-full text-primary-foreground disabled:opacity-40 disabled:cursor-not-allowed"
      >
        Process Document
      </button>
    </div>
  )
}
